import { StatusBar } from "expo-status-bar";
import { useEffect, useState, useCallback } from "react";
import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  TextInput,
  TouchableOpacity,
  Switch,
  Alert,
} from "react-native";
import { useFonts } from "expo-font";
import AsyncStorage from "@react-native-async-storage/async-storage";
import * as SplashScreen from "expo-splash-screen";
SplashScreen.preventAutoHideAsync(); //폰트 가져올떄 씀 - 폰트 로딩중에 스플래시 스크린 계속 띄우는 역할인듯

export default function App() {
  /*화면 어떤거 랜더링할지 바꿔주는 state*/
  const [screenmode, setScreenmode] = useState("login");
  const changeSCRmode = (mode) => {
    setScreenmode(mode);
  };
  //main가는거.
  const fnbacktomain = () => {
    changeSCRmode("main");
    fnmaindata();
  };
  const showalert = (sentense) => {
    Alert.alert("WARNING", sentense);
  };

  //asyncstorage

  const fnsaveuserdata = async (userid, userpw) => {
    await AsyncStorage.setItem("@userid", JSON.stringify(userid));
    await AsyncStorage.setItem("@userpw", JSON.stringify(userpw));
  };
  const fnloaduserdata = async () => {
    const [userId, userPw] = await Promise.all([
      AsyncStorage.getItem("@userid"),
      AsyncStorage.getItem("@userpw"),
    ]);
    if (userId !== null) {
      const data = JSON.parse(userId);
      setLogintext(() => data);
    }
    if (userPw !== null) {
      const data = JSON.parse(userPw);
      setLoginpw(() => data);
    }
  };

  //로그인
  const [logintext, setLogintext] = useState("");
  const logininput = (text) => {
    setLogintext(text);
  };
  const [loginpw, setLoginpw] = useState("");
  const loginpwinput = (text) => {
    setLoginpw(text);
  };
  const fnfetchlogin = async (id, pw) => {
    await fetch(`http://13.209.125.35:8080/login?id=${id}&pw=${pw}`)
      .then(async (response) => {
        if (response.status === 404) {
          window.alert("cannot find id");
        } else if (response.status === 401) {
          window.alert("check id or pw");
        } else if (response.status === 200) {
          await fnsaveuserdata(id, pw);
          fnbacktomain();
        }
      })
      .catch((err) => console.log(err));
  };
  const fnlogin = async (id, pw) => {
    if (id === "") {
      window.alert("id must be filled");
    } else if (pw === "") {
      window.alert("pw must be filled");
    } else {
      fnfetchlogin(id, pw);
    }
  };

  //회원가입
  const [schoolid, setSchoolid] = useState("");
  const fnSchollidinput = (text) => {
    setSchoolid(text);
  };
  const [name, setName] = useState("");
  const fnNameinput = (text) => {
    setName(text);
  };
  const [phonenum, setPhonenum] = useState("");
  const fnPhonenuminput = (text) => {
    setPhonenum(text);
  };
  const [ID, setID] = useState("");
  const fnIDinput = (text) => {
    setID(text);
  };
  const [PW, setPW] = useState("");
  const fnPWinput = (text) => {
    setPW(text);
  };

  const fnfetchsignup = async (named, studentid, phone, id, pw) => {
    await fetch(
      `http://13.209.125.35:8080/signup?named=${named}&studentid=${studentid}&phone=${phone}&id=${id}&pw=${pw}`
    )
      .then(async (response) => {
        if (response.status === 404) {
          window.alert("id should be unique");
        } else if (response.status === 200) {
          setLogintext(id);
          setLoginpw(pw);
          await fnsaveuserdata(id, pw);
          fnbacktomain();
        }
      })
      .catch((err) => console.log(err));
  };

  const fnSignup = async (schoolid, name, phonenum, ID, PW) => {
    if (schoolid === "") {
      window.alert("schoolid must be filled");
    } else if (name === "") {
      window.alert("name must be filled");
    } else if (phonenum === "") {
      window.alert("phonenum must be filled");
    } else if (ID === "") {
      window.alert("ID must be filled");
    } else if (PW === "") {
      window.alert("PW must be filled");
    } else {
      fnfetchsignup(schoolid, name, phonenum, ID, PW);
    }
  };

  //메인
  const fngotowrite = () => {
    changeSCRmode("write");
  };
  const fnmy = () => {
    changeSCRmode("my");
  };
  const [maindataset, setMaindataset] = useState([]);
  const fnmaindata = async () => {
    await fetch(`http://3.39.29.61:8080/main`, {
      method: "post",
      headers: { "Content-Type": "application/json" },
    })
      .then(async (response) => {
        const data = await response.json();
        setMaindataset(data);
      })
      .catch((err) => console.log(err));
  };
  const fnMakemain = () => {
    return maindataset.reverse().map((content) => {
      return (
        <TouchableOpacity
          key={content.id}
          style={styles.main.infobox}
          onPress={() => fngotocomment(content.id)}
        >
          <View style={styles.main.infoheader}>
            <View style={styles.main.infotop}>
              <Text style={styles.main.infotitle}>{content.title}</Text>
              {content.anonymous === 0 ? (
                <Text style={styles.main.infoauthor}>{content.username}</Text>
              ) : null}
            </View>
            <View style={styles.main.infotop}>
              <Text style={styles.main.infodate}>
                {new Date(content.created_in).toISOString().split("T")[0]}
              </Text>
            </View>
          </View>
          <Text style={styles.main.infotext}>{content.description}</Text>
        </TouchableOpacity>
      );
    });
  };
  //글쓰기
  const [isEnabled, setIsEnabled] = useState(false);
  const [anonymous, setAnonymous] = useState(0);
  const toggleSwitch = () => {
    setIsEnabled((previousState) => {
      if (previousState === false) {
        setAnonymous(1);
      } else {
        setAnonymous(0);
      }
      return !previousState;
    });
  };

  const [sendtitle, setSendtitle] = useState("");
  const fnwritetitle = (text) => {
    setSendtitle(text);
  };
  const [senddes, setSenddes] = useState("");
  const fnwritedes = (text) => {
    setSenddes(text);
  };
  const post = (title, description, username, anonymous) => {
    if (title === "") {
      window.alert("title must be filled");
    } else if (description === "") {
      window.alert("description must be filled");
    } else {
      fetch(`http://3.39.29.61:8080/post`, {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          title: title,
          description: description,
          username: username,
          anonymous: anonymous,
        }),
      }).catch((err) => console.log(err));
      setSendtitle("");
      setSenddes("");
      fnbacktomain();
    }
  };
  //my
  const fnlogout = () => {
    fnsaveuserdata("", "");
    setLogintext("");
    setLoginpw("");
    changeSCRmode("login");
  };
  //comment
  const [commentto, setCommentto] = useState([]);
  const [wrtID, setWrtID] = useState("");
  const [commentdata, setcommentdata] = useState([]);
  const getcomment = async (id) => {
    await fetch(`http://3.39.29.61:8080/detgul`, {
      method: "post",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        writing_id: id,
      }),
    })
      .then(async (response) => {
        const data = await response.json();
        setcommentdata(data);
      })
      .catch((err) => console.log(err));
  };
  const fngotocomment = async (id) => {
    setWrtID(id);
    changeSCRmode("comment");
    const objectWithId = maindataset.filter((content) => content.id === id);
    setCommentto(objectWithId);
    getcomment(id);
  };
  const [commenttext, setCommenttext] = useState("");
  const fncommittext = (text) => {
    setCommenttext(text);
  };
  const comment = (writing_id, comment, username, anonymous) => {
    if (comment === "") {
      window.alert("comment must be filled");
    } else {
      fetch(`http://3.39.29.61:8080/comment`, {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          writing_id: writing_id,
          comment: comment,
          username: username,
          anonymous: anonymous,
        }),
      }).catch((err) => console.log(err));
      getcomment(wrtID);
      setCommenttext("");
    }
  };

  const fnMakecomment = () => {
    return commentdata.map((content) => {
      return (
        <View key={content.id} style={styles.comment.commentbox}>
          <Text style={styles.comment.commentdes}>{content.comment}</Text>
          <View style={styles.comment.commentdetail}>
            <Text>
              {content.anonymous === 0 ? content.username : "anonymous"}
            </Text>
            <Text>
              {new Date(content.created_in).toISOString().split("T")[0]}
            </Text>
          </View>
        </View>
      );
    });
  };

  useEffect(() => {
    fnloaduserdata();
  }, []);

  /*폰트 가져오는 방법인데 걍 긁어온거라 잘 모르겠음 ㅋㅋ (+이거 아래에 state하니까 오류나더라 */
  const [fontsLoaded] = useFonts({
    /*이 안에다가 fonts파일 안에 있는 otf,ttf파일 경로 적어주면 됨 ㅎㅎ*/
    Asac: require("./assets/fonts/ChangwonDangamAsac-Bold_0712.otf"),
  });
  const onLayoutRootView = useCallback(async () => {
    if (fontsLoaded) {
      await SplashScreen.hideAsync();
    }
  }, [fontsLoaded]);
  if (!fontsLoaded) {
    return null;
  }

  if (screenmode === "login") {
    return (
      <View
        style={styles.container}
        onLayout={onLayoutRootView} /*onlayout 폰트 가져오는 데 필수라더라.. */
      >
        <View /*로그인*/ style={styles.container}>
          <View style={styles.login.main}>
            <View style={styles.login.title}>
              <Text style={styles.sermo}>SERMO</Text>
            </View>

            <View>
              <TextInput
                style={styles.login.content}
                placeholder="ID"
                onChangeText={logininput}
                value={logintext}
              ></TextInput>
              <TextInput
                style={styles.login.content}
                placeholder="PW"
                onChangeText={loginpwinput}
                value={loginpw}
              ></TextInput>
              <TouchableOpacity
                style={styles.login.submitbtn}
                onPress={() => fnlogin(logintext, loginpw)}
              >
                <View style={{ alignItems: "center" }}>
                  <Text style={{ fontSize: 16 }}>login</Text>
                </View>
              </TouchableOpacity>
              <TouchableOpacity
                style={{ alignItems: "center" }}
                onPress={() => changeSCRmode("signup")}
              >
                <Text style={{ textDecorationLine: "underline" }}>signup</Text>
              </TouchableOpacity>
            </View>
          </View>
          <View style={{ flex: 0.2, backgroundColor: "#ffffff" }}></View>
        </View>
        <StatusBar style="auto" />
      </View>
    );
  } else if (screenmode === "signup") {
    return (
      <View
        style={styles.container}
        onLayout={onLayoutRootView} /*onlayout 폰트 가져오는 데 필수라더라.. */
      >
        <View style={styles.container}>
          <View style={styles.signup.main}>
            <View style={styles.signup.title}>
              <Text style={styles.signup.sermo}>SERMO</Text>
            </View>
            <TextInput
              style={styles.signup.datainput}
              keyboardType="number-pad"
              placeholder="school ID"
              onChangeText={fnSchollidinput}
              value={schoolid}
            ></TextInput>
            <TextInput
              style={styles.signup.datainput}
              placeholder="name"
              onChangeText={fnNameinput}
              value={name}
            ></TextInput>
            <TextInput
              style={styles.signup.datainput}
              keyboardType="number-pad"
              placeholder="phone num"
              onChangeText={fnPhonenuminput}
              value={phonenum}
            ></TextInput>
            <TextInput
              style={styles.signup.datainput}
              placeholder="ID"
              onChangeText={fnIDinput}
              value={ID}
            ></TextInput>
            <TextInput
              style={styles.signup.datainput}
              placeholder="PW"
              onChangeText={fnPWinput}
              value={PW}
            ></TextInput>
            <TouchableOpacity
              style={styles.signup.submitbtn}
              onPress={() => fnSignup(schoolid, name, phonenum, ID, PW)}
            >
              <View style={{ alignItems: "center" }}>
                <Text>submit</Text>
              </View>
            </TouchableOpacity>
            <TouchableOpacity
              style={{ alignItems: "center" }}
              onPress={() => changeSCRmode("login")}
            >
              <Text style={{ textDecorationLine: "underline" }}>back</Text>
            </TouchableOpacity>
          </View>
          <View
            style={{
              flex: 0.15,
              backgroundColor: "#ffffff",
              alignItems: "center",
            }}
          >
            <Text style={{ marginTop: 30, fontSize: 10 }}>
              문의 : 01055646928(20925 황선우)
            </Text>
            <Text style={{ fontSize: 10 }}>
              앱 내에서의 행동에 대한 책임은 본인에게 있습니다.
            </Text>
          </View>
        </View>
        <StatusBar style="auto" />
      </View>
    );
  } else if (screenmode === "main") {
    return (
      <View
        style={styles.container}
        onLayout={onLayoutRootView} /*onlayout 폰트 가져오는 데 필수라더라.. */
      >
        <View style={styles.main.title}>
          <TouchableOpacity onPress={() => fngotowrite()}>
            <Text>write</Text>
          </TouchableOpacity>
          <Text style={styles.main.sermo}>SERMO</Text>
          <TouchableOpacity onPress={() => fnmy()}>
            <Text>my</Text>
          </TouchableOpacity>
        </View>

        <ScrollView
          style={styles.main.main}
          contentContainerStyle={{
            alignItems: "stretch",
            justifyContent: "center",
          }}
        >
          <TouchableOpacity
            style={styles.main.reload}
            onPress={() => fnmaindata()}
          >
            <Text>reload</Text>
          </TouchableOpacity>
          {fnMakemain()}
        </ScrollView>

        <StatusBar style="auto" />
      </View>
    );
  } else if (screenmode === "write") {
    return (
      <View
        style={styles.container}
        onLayout={onLayoutRootView} /*onlayout 폰트 가져오는 데 필수라더라.. */
      >
        <View style={styles.write.header}>
          <TouchableOpacity onPress={() => fnbacktomain()}>
            <Text>back</Text>
          </TouchableOpacity>
          <View style={{ flexDirection: "row", alignItems: "center" }}>
            <Text style={{ fontSize: 15, marginHorizontal: 10 }}>
              anonymous
            </Text>
            <Switch
              trackColor={{ false: "#767577", true: "#BBD0FF" }}
              thumbColor={isEnabled ? "#C8B6FF" : "#f4f3f4"}
              ios_backgroundColor="#3e3e3e"
              onValueChange={() => toggleSwitch()}
              value={isEnabled}
            />
          </View>
        </View>
        <View style={styles.write.main}>
          <TextInput
            style={styles.write.titleinput}
            placeholder="title"
            onChangeText={fnwritetitle}
            value={sendtitle}
          ></TextInput>
          <TextInput
            style={styles.write.disinput}
            placeholder="discription"
            multiline={true}
            onChangeText={fnwritedes}
            value={senddes}
          ></TextInput>
          <View style={{ flexDirection: "row" }}>
            <View style={{ flex: 3 }}></View>
            <TouchableOpacity
              style={styles.write.postbtn}
              onPress={() => post(sendtitle, senddes, logintext, anonymous)}
            >
              <Text style={styles.write.postbtntext}>POST</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  } else if (screenmode === "my") {
    return (
      <View
        style={styles.container}
        onLayout={onLayoutRootView} /*onlayout 폰트 가져오는 데 필수라더라.. */
      >
        <View style={styles.my.head}>
          <TouchableOpacity
            style={styles.my.backbtn}
            onPress={() => fnbacktomain()}
          >
            <Text>back</Text>
          </TouchableOpacity>
          <Text style={styles.my.userid}>{logintext}</Text>
        </View>
        <View style={styles.my.main}>
          <View>
            <TouchableOpacity
              style={styles.my.logoutbtn}
              onPress={() => {
                fnlogout();
              }}
            >
              <Text style={styles.my.btntext}>logout</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  } else if (screenmode === "comment") {
    return (
      <View
        style={styles.container}
        onLayout={onLayoutRootView} /*onlayout 폰트 가져오는 데 필수라더라.. */
      >
        <View style={styles.comment.header}>
          <Text style={styles.comment.sermo}>sermo</Text>
        </View>
        <View style={styles.comment.main}>
          <TouchableOpacity onPress={fnbacktomain}>
            <Text>back</Text>
          </TouchableOpacity>
          <Text style={styles.comment.titletext}>{commentto[0].title}</Text>
          <Text style={styles.comment.destext}>{commentto[0].description}</Text>

          <View>
            <View style={styles.comment.overinput}>
              <Text style={{ fontSize: 31, fontWeight: "600" }}>comment</Text>
              <Switch
                trackColor={{ false: "#767577", true: "#BBD0FF" }}
                thumbColor={isEnabled ? "#C8B6FF" : "#f4f3f4"}
                ios_backgroundColor="#3e3e3e"
                onValueChange={() => toggleSwitch()}
                value={isEnabled}
              />
            </View>
            <TextInput
              style={styles.comment.inputbox}
              placeholder="comment"
              onChangeText={fncommittext}
              multiline={true}
              value={commenttext}
            ></TextInput>
            <TouchableOpacity
              style={styles.comment.commitbtn}
              onPress={() => comment(wrtID, commenttext, logintext, anonymous)}
            >
              <Text>commit</Text>
            </TouchableOpacity>
            <ScrollView>{fnMakecomment()}</ScrollView>
          </View>
        </View>
      </View>
    );
  }
}

